.grid-container {
  height: 100vh;
  display: flex;
}

.body {
  height: 100%;
}

#root {
  height: 100vh;
}

.chat {
  display: flex;
  flex-direction: column;
  flex-grow: 3;
}

.sidebar {
  padding-right: 50px;
  padding-top: 25px;
}

.sidebar-checkbox-label {
  display: inline-block;
  padding: 10px 10px 10px 10px;
}

.conversation-container {
  flex-grow: 3;
}

.message-input-div {
  padding-left: 20px;
  padding-bottom: 25px;
  align-self: left;
}

.message-input {
  width: 90%;
}

.client-message {
  text-align: right;
  padding-right: 11%;
}

.solr-message {
  text-align: left;
}
